import { types } from "../../types/types";

export const showLoading = (dispatch) => {
  dispatch({
    type: types.showLoading,
  });
};

export const hideLoading = (dispatch) => {
  dispatch({
    type: types.hideLoading,
  });
};

export const showLoadingCalendar = (dispatch) => {
  dispatch({
    type: types.showLoadingCalendar,
  });
};

export const hideLoadingCalendar = (dispatch) => {
  dispatch({
    type: types.hideLoadingCalendar,
  });
};

export const showLoadingComentarios = (dispatch) => {
  dispatch({
    type: types.showLoadingComentarios,
  });
};

export const hideLoadingComentarios = (dispatch) => {
  dispatch({
    type: types.hideLoadingComentarios,
  });
};

export const appLimpia = (dispatch) => {
  dispatch({
    type: types.resetState,
  });
};

export const paramsCarga = (resp, dispatch) => {
  const { params } = resp;
  dispatch({
    type: types.paramsCargados,
    payload: {
      params,
    },
  });
};

export const paramsLimpia = (dispatch) => {
  dispatch({
    type: types.paramsPurga,
  });
};
