import { Divider } from "@material-ui/core";
import React, { useContext, useEffect, useState } from "react";
import { AuthContext } from "../../context/AuthContext";
import { cancelaAccionFire } from "../../helpers/messagesUI";

export const BlogComentarioItem = ({ comentario }) => {
  const { id, usuario, comentario: coment } = comentario;
  const { auth } = useContext(AuthContext);
  const [edit, setEdit] = useState(false);
  const [eliminar, setEliminar] = useState({});

  useEffect(() => {
    console.log(eliminar.isConfirmed);
  }, [eliminar]);

  const handleEdit = (e) => {
    e.preventDefault();
    setEdit(!edit);
  };

  const handleDelete = async (e) => {
    e.preventDefault();
    await cancelaAccionFire(setEliminar, "Eliminar comentario");
  };

  return (
    <div
      className="input-group col-md-12 "
      style={{
        flexDirection: "column",
        backgroundColor: auth.uid === usuario._id ? "ActiveCaption" : "with",
        borderRadius: 5,
        marginBottom: 5,
        alignContent: "flex-start",
        borderBlockColor: "lightsalmon",
        border: "1px solid",
      }}
    >
      <small className="ml-2 mb-2">{coment}</small>
      <Divider />
      <div className="m-2">
        <small className="ml-2 mb-2">Por: {usuario.nombre}</small>
        {auth.uid === usuario._id && (
          <>
            <i className=" ml-3 fa fa-pencil-alt" onClick={handleEdit}></i>
            <i className=" ml-4 fa fa-trash-alt" onClick={handleDelete}></i>
            {edit && (
              <i className=" ml-4 fa fa-save" onClick={handleDelete}></i>
            )}
          </>
        )}
      </div>
      {edit && (
        <div>
          <spam>Editando</spam>
        </div>
      )}
    </div>
  );
};
