import { prepareEvents } from "../../helpers/prepareEvents";
import { types } from "../../types/types";

// Eventos
export const registraEventos = (resp, dispatch) => {
  // const { eventos } = resp;
  const eventos = prepareEvents(resp.eventos);
  dispatch({
    type: types.eventosCargados,
    payload: {
      eventos: eventos,
    },
  });
};
export const seleccionaEvento = (payload, dispatch) => {
  dispatch({
    type: types.eventoSeleccionado,
    payload,
  });
};
export const eliminaEventoSelecciondo = (dispatch) => {
  dispatch({
    type: types.eventoSeleccionadoDelete,
  });
};
export const LimpiaEventos = (dispatch) => {
  dispatch({
    type: types.eventosPurga,
  });
};
export const addEvento = (payload, dispatch) => {
  dispatch({
    type: types.eventoAdd,
    payload,
  });
};
export const updateEvento = (payload, dispatch) => {
  dispatch({
    type: types.eventoUpdate,
    payload,
  });
};
export const deleteEvento = (payload, dispatch) => {
  dispatch({
    type: types.eventoDelete,
    payload,
  });
};
