import React, { createContext, useReducer } from "react";
import Swal from "sweetalert2";
import { fetchConToken } from "../helpers/fetch";
import { prepareEventNew, prepareEventEdit } from "../helpers/prepareEvents";
import { appReducer } from "../reducers/appReducer";
import {
  showLoading,
  hideLoading,
  appLimpia,
  showLoadingCalendar,
  hideLoadingCalendar,
  paramsCarga,
  paramsLimpia,
  showLoadingComentarios,
  hideLoadingComentarios,
} from "./actions/appActions";
import {
  registraEventos,
  seleccionaEvento,
  LimpiaEventos,
  updateEvento,
  deleteEvento,
  addEvento,
  eliminaEventoSelecciondo,
} from "./actions/eventosActions";
import { initialState } from "./initialState/appInitialState";

export const AppContext = createContext();

export const AppProvider = ({ children }) => {
  const [state, dispatch] = useReducer(appReducer, initialState);

  //APP
  const showModalLoading = () => {
    showLoading(dispatch);
  };

  const hideModalLoading = () => {
    hideLoading(dispatch);
  };

  const purgaApp = () => {
    appLimpia(dispatch);
  };

  //Eventos
  const limite = 1000;
  const desde = 0;

  const showModalLoadingCalendar = () => {
    showLoadingCalendar(dispatch);
  };
  const hideModalLoadingCalendar = () => {
    hideLoadingCalendar(dispatch);
  };
  const cargaEventos = async (orden = "asc") => {
    try {
      const resp = await fetchConToken(
        `calendario?limite=${limite}&desde=${desde}&orden=${orden}`
      );
      if (resp.ok) {
        registraEventos(resp, dispatch);
      } else {
        LimpiaEventos(dispatch);
        Swal.fire("error", resp.msg);
      }
    } catch (error) {
      console.log(error);
    }
  };

  const cargaParams = async () => {
    try {
      const resp = await fetchConToken("parametros");
      if (resp.ok) {
        paramsCarga(resp, dispatch);
      } else {
        paramsLimpia(dispatch);
        Swal.fire("error", resp.msg);
      }
    } catch (error) {
      console.log(error);
    }
  };

  const limpiaParams = async () => {
    paramsLimpia(dispatch);
  };

  const selecEvento = (evento) => {
    seleccionaEvento(evento, dispatch);
  };
  const agregaEvento = (data) => {
    addEvento(prepareEventNew(data), dispatch);
  };
  const editarEvento = (data) => {
    updateEvento(prepareEventEdit(data), dispatch);
  };
  const eliminarEvento = (data) => {
    deleteEvento(data, dispatch);
  };
  const purgaEventoSelecciondo = () => {
    eliminaEventoSelecciondo(dispatch);
  };

  //Blog
  const showModalComentarios = () => {
    showLoadingComentarios(dispatch);
  };

  const hideModalComentarios = () => {
    hideLoadingComentarios(dispatch);
  };
  return (
    <AppContext.Provider
      value={{
        state,
        showModalLoading,
        hideModalLoading,
        purgaApp,
        cargaEventos,
        cargaParams,
        selecEvento,
        agregaEvento,
        limpiaParams,
        editarEvento,
        eliminarEvento,
        showModalLoadingCalendar,
        hideModalLoadingCalendar,
        purgaEventoSelecciondo,
        showModalComentarios,
        hideModalComentarios,
      }}
    >
      {children}
    </AppContext.Provider>
  );
};
